<template>
  <div>
    <m-header title="UTAQI 教育" :bg="true" fixed>
    </m-header>
    
    <div class="page-content">
    	<m-swipe swipeid="swipe01" :autoplay="2000" paginationDirection="right">
	        <div class="swiper-slide" slot="swiper-con"><img src="../../assets/img/banner/01.jpg" alt=""></div>
	        <div class="swiper-slide" slot="swiper-con"><img src="../../assets/img/banner/02.jpg" alt=""></div>
	        <div class="swiper-slide" slot="swiper-con"><img src="../../assets/img/banner/03.jpg" alt=""></div>
		</m-swipe>

		<m-cell title="提醒" icon>
			<img src="../../assets/img/ic_mine_notification.png" slot="icon">
			<a href="javascript:;" slot="cell-right"><img src="../../assets/img/ic_arrow_gray_small.png" alt=""></a>
		</m-cell>
		<m-cell title="设置">
			<a href="javascript:;" slot="cell-right"><img src="../../assets/img/ic_arrow_gray_small.png" alt=""></a>
		</m-cell>
		
		<!-- 热门 -->
		<div class="hot-wrap">
			<m-cell title="HOT" label="hot">
				<a href="javascript:;" slot="cell-right">更多</a>
			</m-cell>
			
			<m-cell-media v-for="(item,index) in this.hotData" :author="item.target.author.name" :column="item.source_cn" :img="item.target.cover_url" :key="item.id">
				<span slot="title">{{item.title}}</span>
				<span slot="describe">{{item.target.desc}}</span>
			</m-cell-media>
		</div>

		<!-- 推荐 -->
		<div class="recommend-wrap">
			<m-cell title="推荐" label="recommed"></m-cell>
			<m-cell-media  :author="item.target.author.name" :column="item.source_cn" :bg="item.target.cover_url" v-for="(item,index) in recommendData" :key="item.id">
				<span slot="title">{{item.title}}</span>
				<span slot="describe">{{item.target.desc}}</span>
			</m-cell-media>
		</div>
    </div>
  </div>
</template>

<script>
import mHeader from '../../components/header'
import mSwipe from '../../components/swipe'
import mCell from '../../components/cell'
import mCellMedia from '../../components/cell-media'
export default {
  name: 'index',
  components:{
    mHeader,
    mSwipe,
    mCell,
    mCellMedia
  },
  data(){
  	return{
  		recommendData:[],
  		hotData:[]
  	}
  },
  created(){
  	this.fetchData();
  },
  methods:{
  	fetchData(){
  		this.axios.get('../static/data/index.json').then((response)=>{
  			console.log(response);
  			let data = response.data.recommend_feeds;
  			let recommed = [];
  			let hot = [];
  			for(var i in data){
  				if(data[i].card && data[i].card.name =='为你推荐'){
  					recommed.push(data[i]);
  				}else{
  					hot.push(data[i]);
  				}
  			}
  			this.recommendData = recommed;
  			this.hotData = hot;
  			console.log(this.recommendData);
  			console.log(this.hotData);
  		})
  	}
  }
}
</script>

<style lang="less">
	header.m-header {
	    padding: 0 0 0 10px;
	}
	.is-fixed~.page-content{
		padding-top: 44px;
		padding-bottom: 50px;
	}
	.top-search {
		.search-wrap {
			width: 100%;
			height: 30px;
			background: #fff;
			border-radius: 4px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			color: #c0c0c0;
			padding: 0 12px;
			.placeholder {
				flex: 1;
				text-align: left;
				padding-left: 12px;
			}
			img {
				width: 20px;
				height: 20px;
			}
		}
	}
	.hot-wrap,
	.recommend-wrap {
		padding-top: 12px;
	}
	a{
		text-decoration: none;
	}
</style>



