import Vue from 'vue'
import Router from 'vue-router'
import Index from '../pages/Index/Index'
import Exam from '../pages/Exam/Exam'
import Live from '../pages/Live/Live'
import Mine from '../pages/Mine/Mine'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/exam',
      name: 'Exam',
      component: Exam
    },
    {
      path: '/live',
      name: 'Live',
      component: Live
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/Index',
      redirect:'/'
    },
    {
      path: '*',
      redirect:'/'
    }
  ]
})



